@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap');
@import url('../../__variables.scss');

html, body{
    margin:0;
    padding:0;
}

$primary: #7076FE;
$secondary: #4B556B;
$white: #F9F9F9;

@media only screen and (min-width:480px){
    .homepage{
        box-shadow: inset 0 0 0 1000px rgba(0,0,0,.8);
        background-image:url('../../../public/background.png');
        background-size:cover;
        height:80vh;
        width:100%;
        font-family:'Poppins',sans-serif;
    
        .container{
            width:70%;
            margin-left:15%;
            height:100%;
            
        .row{
            width:100%;
            height:100%;
            .col-2{
                width:50%;
                float:left;
                position: relative;
                height:100%;
    
                .content{
                    position: absolute;
                    top:50%;
                    left:50%;
                    transform: translate(-50%, -50%);
                    width:100%;
    
                    h1{
                        color:$primary;
                    }
        
                    h3{
                        font-size:1.6rem;
                        color:white;
                        font-weight:500;
                    }
        
                    h2{
                        background-color:$primary;
                        color:white;
                        font-weight:600;
                        font-size:30px;
                        padding:10px;
                        width:63%;
                        border-radius:10px;
                    }
        
                    button{
                        font-size:24px;
                        padding:5px 45px;
                        border-radius:10px;
                        background-color:$primary;
                        color:white;
                        font-weight:600;    
                        cursor:pointer;
                        transition:0.5s;
                        &:hover{
                            background-color:rgba(246,87,4,0.5);
                        }
        
                    }
                }
               
            }
        }
    }        
    }
}


@media only screen and (max-width:480px){
    .homepage{
        box-shadow: inset 0 0 0 1000px rgba(0,0,0,.8);
        background-image:url('../../../public/background.png');
        background-size:cover;
        width:100%;
        font-family:'Poppins',sans-serif;
        height:110vh;

        .container{
            width:90%;
            margin-left:5%;
            height:100%;
            
        .row{
            width:100%;
            height:100%;

            .col-2{
                width:100%;
                
                .comp-container{
                    width:95%;
                    background-image:url('../../money.jpg');
                    background-position: center;
                    padding-bottom:20px;
                    background-size:130%;
                    box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.79);
                    border-radius:10px;
                    margin-top:2rem;
                    border-style:solid;
                    border-color:#7076FE;
                    border-width:3px;

                    h2{
                        color:white;
                        padding:15px;
                        margin:0;

                    }

                    h4{
                        color:white;
                        font-weight:400;
                        padding-left:15px;
                        margin:0;
                    }

                    button{
                        font-size:17px;
                        padding:6px 35px;
                        border-radius:10px;
                        background-color:$primary;
                        color:white;
                        font-weight:600;    
                        margin-left:15px;
                        margin-top:10px;
                        border:none;
                        cursor:pointer;
                        transition:0.5s;
                        &:hover{
                            background-color:rgba(246,87,4,0.5);
                        }
                    }
                }

                .content{
                    padding-top:3rem;
                    width:100%;
                    
                    h1{
                        color:$primary;
                        line-height:42px;
                        margin:0;
                        padding:0;
                    }
        
                    h3{
                        font-size:1.2rem;
                        color:white;
                        font-weight:500;
                    }
        
                    h2{
                        background-color:$primary;
                        border-style:solid;
                        border-width:3px;
                        color:white;
                        font-weight:600;
                        font-size:30px;
                        padding:10px;
                        width:90%;
                        border-radius:10px;
                        margin:0;

                        button{
                            font-size:17px;
                            padding:6px 35px;
                            border-radius:10px;
                            background-color:white;
                            color:$primary;
                            font-weight:600;    
                            margin-top:10px;
                            border:none;
                            cursor:pointer;
                            transition:0.5s;
                        }
                    }
        
                    button{
                        font-size:24px;
                        padding:5px 45px;
                        border-radius:10px;
                        background-color:$primary;
                        color:white;
                        font-weight:600;    
                        cursor:pointer;
                        transition:0.5s;
                        border:none;
                        &:hover{
                            background-color:rgba(246,87,4,0.5);
                        }
        
                    }
                }
               
            }
        }
    }        
    }
}